import React, { useEffect, useState } from 'react';
import { Tag } from 'antd';
import {
  EyeOutlined,
  UserOutlined
} from '@ant-design/icons';


function FooterLayout() {

  const [day, setDay] = useState(0);
  const [hour, setHour] = useState(0);
  const [minute, setMinute] = useState(0);
  const [second, setSecond] = useState(0);

  useEffect(()=>{
    const interval = setInterval(() => {
      const startTime = 1699600494654;
      let nowTime = new Date().getTime();
      // 
      let betweenTime = parseInt((nowTime - startTime) / 1000);
      setDay(Math.floor(betweenTime / 86400));
      setHour(Math.floor((betweenTime % 86400) / 3600));
      setMinute(Math.floor(((betweenTime % 86400) % 3600) / 60));
      setSecond(Math.floor(((betweenTime % 86400) % 3600) % 60));
    }, 1000);
  
    return () => {
      clearInterval(interval);
    };
  }, [])

  return (
    <div className='bottom-div'>
      <p>
        <span style={{marginRight:'8px'}}></span>
        <Tag color="magenta">{day}</Tag><span style={{marginRight:'8px'}}></span>
        <Tag color="orange">{hour}</Tag><span style={{marginRight:'8px'}}></span>
        <Tag color="cyan">{minute}</Tag><span style={{marginRight:'8px'}}></span>
        <Tag color="blue">{second}</Tag><span style={{marginRight:'8px'}}></span>
      </p>
      <p>
        <span id="busuanzi_container_site_pv"><EyeOutlined style={{color:'geekblue',marginRight:'5px'}}/>&nbsp;
          <Tag color="geekblue"><span id="busuanzi_value_site_pv"></span></Tag>
        </span>
        <span style={{margin:'0 5px'}}></span>
        <span id="busuanzi_container_site_uv"><UserOutlined style={{color:'purple',marginRight:'5px'}}/>&nbsp;
          <Tag color="purple"><span id="busuanzi_value_site_uv"></span></Tag>
        </span>
      </p>
    </div>
  );
}


export default FooterLayout;